.main-page-form {
position: absolute;
bottom: -100%;
opacity: 1;

    .input-number-wrapper {
        display: flex;
        justify-content: space-between;
        margin-bottom: 32px;
    }

    
}

.open-form {
    position: relative;
    bottom: auto;
    padding: 0 20px;
    font-size: 14px;
    font-weight: bold;
    line-height: 40px;
    padding-bottom: 50px;
    background: white; 
    box-shadow: 0px 10px 20px 0px rgba(102, 98, 98, 0.5);

    @media (min-width: 768px) { 
        position: absolute;
        width: 570px;
        padding: 50px;
        margin: 0 auto;
        transform: translate(-50%, 0);
        animation: fade-in 0.6s;
        opacity: 1;
        bottom: auto;
        left: 50%;
        box-sizing: border-box;
    }
}
