.input-content {
    display: block;
    padding: 13px 0;
    font-size: 14px;
    font-family: "pt sans", "arial";
    text-transform: uppercase;    
    font-weight: bold;
    min-width: 250px;
    color: black;

    @media (min-width: 768px) {
        display: flex;
        justify-content: space-between;
    }

    .input { 
        display: block;
        height: 40px;
        width: 100%;
        padding: 10px 15px;
        box-sizing: border-box;
        font-size: 14px;
        font-weight: bold;
        text-transform: uppercase;
        border: none;
        color: black;
        outline: none;
        background: $lightgray;

        &--calendar {
            background: $lightgray url(../../img/icon-calendar.svg) no-repeat 97% 6px;

            @media (min-width: 768px) {
                width: 350px;
            }
        }

        &:hover {
            background-color: $darkgray; 
        }

        &:focus,
        &:active {
        background-color: white;
        outline:none;
        border: 2px solid $darkgray;
        box-sizing: border-box;
        }
    }

    &--number {
        position: relative;
        min-width: 114px;

        @media (min-width: 768px) {
            min-width: 233px;

            &:last-of-type {
                min-width: 180px;
            }

            .input {
                width: 114px;
            }
        }

        .input {
            text-align: center;
        }

        .minus {
            position: absolute;
            width: 38px;
            height: 35px;
            border: 0;
            background: none;
            top: 54px;
            left: 0px;

            @media (min-width: 768px) {
                transform: translate(0, -50%);
                top: 50%;
                left: auto;
                right: 75px;
            }

            &::before {
                content: "";
                position: absolute;
                display: block;
                width: 10px;
                height: 2px;
                background-color: $darkest-gray;
                transform: translate(-50%, -50%);
                top: 50%;
                left: 50%;
            }

            &:hover,
            &:focus {
                outline: none;

                &::before {
                    background-color: black;
                }
            }

            &:active {
                outline: none;

                &::before {
                    background-color: $main;
                }
            }
        }

        .plus {
            position: absolute;
            width: 38px;
            height: 35px;
            border: 0;
            background: none;
            top: 54px;
            right: 0px;

            @media (min-width: 768px) {
                transform: translate(0, -50%);
                top: 50%;
            }

            &::before,
            &::after {
                content: "";
                position: absolute;
                display: block;
                width: 10px;
                height: 2px;
                background-color: $darkest-gray;
                transform: translate(-50%, -50%);
                top: 50%;
                left: 50%;
            }

            &::after {
                transform: translate(-50%, -50%) rotate(90deg);
                top: 50%;
                left: 50%;
            }

            &:hover,
            &:focus {
                outline: none;

                &::before,
                &::after {
                    background-color: black;
                }
            }

            &:active {
                outline: none;

                &::before,
                &::after {
                    background-color: $main;
                }
            }
        }
    }
    

    
}

