.intro {
    position: relative;
    background-image: url(../../img/photo-background@1x.jpg);
    background-repeat: no-repeat;
    background-size: 180%;
    background-position: 50% 20%;


    &::before {
        content: "";
        position: absolute;
        height: 15vw;
        bottom: -1px;
        width: 100%;
        background-image: linear-gradient(to right top, #fff 25%, transparent 25%),
                          linear-gradient(to left top, #fff 25%, transparent 25%);
    }

    @media (min-width: 768px) {
        &::before {
            height: 12vw; 
        }  

        &::after {
            content: "";
            position: absolute;
            width: 70%;
            height: 12vw;  
            bottom: -1px;
            left: 50%;
            transform: translate(-50%,0);
            background-image: linear-gradient(to right top, #fff 25%, transparent 25%),
                              linear-gradient(to left top, #fff 25%, transparent 25%);
        }
    }

    &__titles {
        display: flex;
        flex-direction: column;
        align-items: center;

        .tagline {
            padding: 60px 0;
            margin: 0 auto;
            width: 90vw;

            @media (min-width: 768px) {
                width: 70vw;
            }

            @media (min-width: 1280px) {
                width: 50vw;
            }
        }
    }
}