.main-footer {
    position: absolute;
    width: 100%;
    padding: 30px;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.9);
    box-sizing: border-box;
    text-align: center;

    @media (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 50px; 
    }

    @media (min-width: 1280px) {
        padding: 12px 100px;
    }


    .link {
        display: block;
        color: black;

    }
 
    .social-buttons-list {
        margin: 0 auto;
        padding: 40px;

        @media (min-width: 768px) {
            margin-left: 5%;
            margin-right: 0%;
        }
    }

    .partners {

        .link {
            position: relative;
            display: inline-block;
            padding-right: 40px;
            font-size: 14px;
            font-weight: normal;

            &--by {

                &::after {
                    content: "";
                    position: absolute;
                    display: block;
                    width: 25px;
                    height: 30px;
                    background: url(../../img/logo-glukhanko.svg) no-repeat;
                    background-size: contain;
                    transform: translate(-20%, -20%);
                    top: 0;
                    right: 0;

                }

                &:hover {

                    &::after {
                        opacity: 0.3;   
                    }
                }
                
                &:active {

                    &::after {
                        background: url(../../img/logo-glukhanko-blue.svg) no-repeat;
                    }
                }
            }

            &--for {

                &::after {
                    content: "";
                    position: absolute;
                    display: block;
                    width: 27px;
                    height: 33px;
                    background: url(../../img/logo-htmlacademy.svg) no-repeat;
                    background-size: contain;
                    transform: translate(-10%, -20%);
                    top: 0;
                    right: 0;
                }

                &:hover {

                    &::after {
                        opacity: 0.3;   
                    }
                }
                
                &:active {

                    &::after {
                        background: url(../../img/logo-htmlacademy-blue.svg) no-repeat;
                    }
                }
            }
        }
    }
 }
