.page-header {
    position: relative;
    text-align: center;
    font-size: 14px;
    line-height: 28px;
    padding: 45px 35px;

    &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 12vw;  
        bottom: -1px;
        left: 50%;
        transform: translate(-50%,0);
        background-image: linear-gradient(to right top, $main 25%, transparent 25%),
                          linear-gradient(to left top, $main 25%, transparent 25%);
    }

    @media (min-width: 768px){
        
        &::after {
            display: none;
        }
    }

    .sub-title {
        padding-bottom: 30px;
    }
}

