.main-nav {
    position: relative;
    z-index: 5;

    &__list {
        list-style-type: none;
        margin: 0;
        padding: 0;

        @media (min-width: 768px) {
            display: flex;
        }

        &-item {

            @media (min-width: 768px) {
                width: 20%;

                &:nth-of-type(2) {
                    margin-right: auto;
                }
            }

            &-link{
                display: block;
                padding: 20px;
                font-size: 18px;
                font-weight: bold;
                text-align: center;
                text-decoration: none;
                color: white; 

                background-color: $main;
                box-shadow: inset 0 1px 0 0px #9ac2db, 
                            inset 0 -1px 0 0px #678fa8;
                         
                &:hover {
                    background: $darkblue1;
                }
            
                &:active {
                    background: $darkblue2;
                }  

                @media (min-width: 768px) {
                    background: none;
                    color: black;
                    box-shadow: none;
                    font-size: 14px;

                    &:hover { 
                        background: none;
                        color: $main;
                    }

                    &:active {
                        background: none;
                        color: $darkgray;
                    }

                }
            }
        }
    }

    &__close-btn {
        position: absolute;
        background: none;
        border: none;
        height: 23px;
        width: 23px;
        top: 17px;
        right: 20px;

        &::before,
        &::after {
            content: "";
            position:absolute;
            width: 5px;
            height: 25px;
            background-color: white;
            transform: translate(-50%, -50%) rotate(45deg);
            top: 50%;
            left: 50%;
            border: none;
            cursor: pointer;
        }

        &::after {
            transform: translate(-50%, -50%) rotate(135deg);
        }

        @media (min-width: 768px) {
            display: none;
        }
    }
}