.main {
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    background-color: $lightgray;

    hr {
        border: none;
        margin: 0 20px;
        height: 1px;
        background-color: $darkgray;
    }

    &__features {
        padding-top: 40px;
        background: white;

        @media (min-width: 768px) {
            display: flex; 
            padding-top: 160px; 
            padding-bottom: 60px;    
        }
    }

    .gray-card-wrapper {
        @media (min-width: 768px) {
            display: flex;
        }
    }
}