.social-buttons-list {
    list-style: none;
    display: flex;
    width: 162px;
    padding: 0;
    justify-content: space-between;

    &__item {
        width: 50px;
        height: 50px;
        background-color: $main;

        .link {            
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
        }

        &--twitter {
            background: $main;

            &:hover {
                background-color: $darkblue1;
            }

            &:active {
                background-color: $darkblue2; 

                svg {
                    fill: #81b3d2;
                }
            }
        }

        &--facebook {

            &:hover {
                background-color: $darkblue1;
            }

            &:active {
                background-color: $darkblue2;

                svg {
                    fill: #81b3d2;
                }
            }
        }

        &--youtube {

            &:hover {
                background-color: $darkblue1;
            }

            &:active {
                background-color: $darkblue2;

                svg {
                    fill: #81b3d2;
                }
            }
        }
    }
}