.feature {
    position: relative;
    padding-left: 70px;
    padding-right: 70px;
    padding-bottom: 40px;
    text-align: left;

    
    @media (min-width: 768px) {
        padding: 0 5%;
        width: calc(100% / 3);
        text-align: center;
    }

    .sub-title {
        padding-left: 0;
        padding-bottom: 20px;

        @media (min-width: 768px) {
            padding-right: 0;
        }
    }

    &--hotels {

        .sub-title {

            &::before {
                content:"";
                position: absolute;
                width: 40px;
                height: 40px;
                background-image: url(../../img/icon-house.svg);
                background-size: 40px 40px;
                top: -12px;
                left: 20px;

                @media (min-width: 768px) {
                    width: 77px;
                    height: 73px;
                    transform: translate(-50%, 0);
                    background-size: 77px 73px;
                    top: -95px;
                    left: 50%;
                }
            }
        }
    }

    &--food {

        @media (min-width: 768px) {
            order: 2;
        }

        .sub-title {

            &::before {
                content:"";
                position: absolute;
                width: 40px;
                height: 40px;
                background-image: url(../../img/burger.jpg);
                background-size: 40px 40px;
                top: -12px;
                left: 20px;

                @media (min-width: 768px) {
                    width: 73px;
                    height: 72px;
                    transform: translate(-50%, 0);
                    background-size: 73px 72px;
                    top: -95px;
                    left: 50%;
                }
            }
        }
    }
    
    &--gift {

        .sub-title {

            &::before {
                content:"";
                position: absolute;
                width: 40px;
                height: 40px;
                background-image: url(../../img/icon-gift.svg);
                background-size: 40px 40px;
                top: -12px;
                left: 20px;

                @media (min-width: 768px) {
                    width: 65px;
                    height: 75px;
                    transform: translate(-50%, 0);
                    background-size: 65px 76px;
                    top: -95px;
                    left: 50%;
                }
            }
        }
    }

}