.card {
    padding: 35px 35px;
    background: $lightgray;

    @media (min-width: 768px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: calc(100% / 3);
    }

    &-number {
        position: relative;
        display: block;
        width: 20px; 
        height: 25px;
        margin: 0 auto;
        padding: 10px 0;

        &::before,
        &::after {
            content: "";
            position: absolute;
            width: 10px;
            height: 2px;
            background: black;
            top: 50%;
        }

        &::before {
            left: -15px;  
        }

        &::after {
            right: -15px;  
        }
    }

    &--lightblue {
        background: $main;
        color: white;
        padding: 45px auto;
        vertical-align: middle;

        .card-number {

            &::before,
            &::after {
                background: white;
            }
        }
    }
}