.btn {    
    display: block;    

    width: 100%; 
    padding: 11px;       
    border: none;

    text-decoration: none;
    text-transform: uppercase;  
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    line-height: 22px;
    outline: none;

    background: $main;
    color: white;

    box-sizing: border-box;

    &:hover {
        background: $darkblue1;
        outline: none;
    }

    &:active {
        background: $darkblue2;
        color: $main;
    }
}

.btn-brown {
    background: $brown;
    width: 100%; 
    color: white;
    box-sizing: border-box;
    outline: none;

    &:hover {
        background: $darkbrown1; 
    }

    &:active {
        background: $darkbrown2; 
        color: $brown;
    }
}