.page-footer {

    &__card {
        padding: 35px 0;
        padding-bottom: 0;
        text-align: center;
        font-size: 14px;
        line-height: 25px;

        @media (min-width: 768px) {
            padding: 0;
            padding-top: 35px;
        }
        
        &-text { 
            display: block;
            margin: 30px;

            .text-break-xs {
                display: none;
            }

            @media (min-width: 768px) {
                text-align: center;
                .text-break-xs {
                    
                    display: inline;
                }
            }
        }

        .title {

            @media (min-width: 768px) {
                padding-top: 30px;
            }            
        }
    }

    .btn-brown {

        &--open-form {
            display: block;
            max-width: 570px;
            padding: 33px;
            margin: 0 auto;
            font-size: 18px;
            font-weight: bold;
            text-transform: uppercase;
            border: none;
            box-sizing: border-box;
        }
    } 

    .main-page-form {

        .input-content {
            
            &--number {
                max-width: 35%;
            }
        }
    }
}