.banner {
    width: 100%;

    @media (min-width: 768px) {
        display: flex;
    }

    &-img {
        height: 258px;
        width: 100%;
        background: url(../../img/photo-city@1x.jpg) no-repeat 0 0;
        background-size: cover;
    }

    &--left {

        .banner-img {
            background: url(../../img/photo-bridge@1x.jpg) no-repeat 30% 0; 
            background-size: cover;
        }

        .card {
            @media (min-width: 768px) {
                order: 2;
            }
        }
    }
}