.main-header {
    position: relative;
    height: 22px;
    padding: 16px 20px;
    background:  white;

    @media (min-width: 768px) {
        height: auto;
        padding: 0;
    }

    .main-nav{
        position: absolute;
        width: 100%;
        bottom: 100%;
        left: 0;

        @media (min-width: 768px) {
            position: relative;
            bottom: 0;
            left: 0;
        }
    }

    &__logo {
        position: absolute;
        background-color: $main;
        width: 100px;
        height: 100px;
        transform: translate(-50%, 0) rotate(45deg);
        top: -50px;
        left: 50%;
        z-index: 2;
        border-radius: 0 0 10px 0;

        img {
            position: absolute;
            display: block;
            height: 55px;
            width: 67px;
            transform: rotate(-45deg);
            left: 36px;
            top: 42px;
        }

        @media (min-width: 768px) {
            display: block !important;
        }
    }

    &.open{

        .main-nav {
            top: 0;
            bottom: auto;
        }

        .main-header__logo {
            display: none;
        }

        .main-header__trigger-btn {
            display: none;
        }
    }

    &__trigger-btn {
        position: absolute;
        width: 23px;
        height: 20px;
        right: 20px;
        background: none;
        border: none;
        border-bottom: 12px double $main; 
        border-top: 4px solid $main; 
        cursor: pointer;

        &:hover {
            outline: none;
            border-bottom: 12px double $darkblue1; 
            border-top: 4px solid $darkblue1;
        }

        &:active {
            outline: none;
            border-bottom: 12px double $darkblue2; 
            border-top: 4px solid $darkblue2;
        }

        @media (min-width: 768px) {
            display: none;
        }
    }

}
